import { Injectable } from '@angular/core';
import { ServiceStatus } from '../models/service-status.enum';
import { Observable } from 'rxjs';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Kind } from '../models/kind';
import { Option } from '../models/option';

@Injectable({
  providedIn: 'root'
})
export class OptionService {

  constructor(private _http: HttpClient) { }

  asyncGetKinds(): Observable<Array<Kind>> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this._http.get(`/api/setup/kinds`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const primitiveKinds = data.body;
        console.log(data.body);
        const kinds: Array<Kind> = primitiveKinds.map((obj) => {
          const k = new Kind();
          k.name = obj.name;
          k.deleted = obj.deleted;
          k._id = obj._id;
          return k;
        });
        observer.next(kinds);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

  asyncGetOptions(kindId: string): Observable<Array<Option>> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this._http.get(`/api/setup/options/${kindId}/`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const primitiveOptions = data.body;
        const options: Array<Option> = primitiveOptions.map((obj) => {
          const o = new Option();
          o.name = obj.name;
          o.deleted = obj.deleted;
          o._id = obj._id;
          o.kind = obj.kind;
          return o;
        });
        observer.next(options);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }

}
